.SearchContainer {
  max-width: 1500px;
  margin: 0 auto;
}


.TodoSearch {
  background: #F9FBFC;
  border-radius: 6px;
  border: 2px solid #202329;
  height: 64px;
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  width: 100%;
  box-sizing: border-box;
}

.TodoSearch::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: rgb(57, 121, 216);
}
